
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.left-box {
    width: 34%;
    margin-top: 60px;
}
.btn-list{
    overflow: hidden;
    line-height: 30px;
}
.btn-list button{
    margin-left: 5px;
}
.result-box{
    float: right;
    width: 60%;
    position: absolute;
    right: 20px;
    top: 20px;
}
.result-item{
    text-align: left;
    border: solid 1px black;
    height: 70vh;
    overflow-y: auto;
}
.result-item span{
    display: inline-block;
    width: 55px;
    text-align: center;
    margin-bottom: 10px;
}
.four{
    color: #0067ed;
}
.five{
    color: red;
}
